import { Icon, InputItem, WhiteSpace } from 'antd-mobile';
import React from 'react';

interface PickItemProps {
  extra?: string;
  onClick?: () => void;
  placeholder: string;
  value?: string;
}

const PickItem: React.FC<PickItemProps> = (props) => {
  const { extra, onClick, placeholder, value } = props;
  console.log('extra+++', extra);
  return (
    <div onClick={onClick}>
      <InputItem
        placeholder={placeholder || '请选择'}
        editable={false}
        extra={<Icon type="down" />}
        value={value || (extra && extra !== '请选择' ? extra : '')}
      />
      <WhiteSpace size="lg" />
    </div>
  );
};

export default PickItem;
