import { Button } from 'antd-mobile';
import { ButtonProps } from 'antd-mobile/lib/button';
import styled from 'styled-px2vw';

const themeColor = '#73C08A';
interface PrimaryButtonProps extends ButtonProps {
  mt?: string;
}

export const Page = styled.div`
  padding: 40px 32px 0;
  overflow: auto;
  height: 100vh;
  input {
    font-size: 32px !important;
  }
  .am-checkbox-agree {
    margin-left: 0;
  }
  .am-input-item {
    padding-left: 0 !important;
    .am-list-line {
      padding-left: 30px;
    }
  }
`;

export const PageTitle = styled.div`
  text-align: center;
  padding-bottom: 48px;
  font-size: 40px;
  letter-spacing: 20px;
  font-weight: bold;
`;

export const PrimaryButton = styled(Button)<PrimaryButtonProps>`
  /* height: 100px;
  line-height: 100px;
  border-radius: 50px; */
  margin-top: ${(props): string | number => props.mt || 0};
`;

export const OthersBox = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 20px 32px;
`;

export const OthersItem = styled.span`
  color: ${themeColor};
`;

export const DrawerBox = styled.div`
  height: 75vh;
  background-color: #fff;
  padding: 0 30px;
`;

export const ButtonBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 100px;
`;
